
.part-loading-box .spin {
    font-size: 1em;
    opacity: 1;
    position: static;
    visibility: visible;
    color: #2db7f5;
    display: inline-block;
    text-align: center;
    -webkit-transition: opacity .3s cubic-bezier(.78, .14, .15, .86);
    transition: opacity .3s cubic-bezier(.78, .14, .15, .86);
}

.part-loading-box .spin .dot {
    -webkit-animation: spin .9s infinite linear alternate;
    animation: spin .9s infinite linear alternate;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    display: inline-block;
    background-color: blue;
}

.part-loading-box .spin .second {
    margin-left: 1em;
    background-color: red;
    animation-timing-function: ease-in;
}

.part-loading-box .spin .third,
.loader-box-3 .spin .third {
    margin-left: 1em;
    background-color: yellow;
    animation-timing-function: ease-out;
}

@-webkit-keyframes spin {
    100% {
        background-color: transparent;
        transform: translateY(30px);
    }
}
@keyframes spin {
    100% {
        background-color: transparent;
        transform: translateY(30px);
    }
}

/* part loading */
.part-loading-box{
    display: flex;
}

.part-loading-box .spin{
    font-size: 0.5em;
}


/* loading box */
.loading-box {
    width: 100%;
    height: 100%;
    min-height: 10em;
    background-color: rgb(243, 243, 243);
    border-radius: inherit;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}



